//pages and components
import Nav from "./components/Nav";
import Footer from './components/Footer';
import GlobalStyle from "./style/globalstyles";
import {AnimatePresence} from 'framer-motion';
import { Switch, Route, useLocation } from "react-router-dom";
import Home from "./pages/home";
import PourEnfants from './pages/pourEnfants';
import PourAdultes from './pages/pourAdultes';
import PourquoiUnePscyhotherapie from './pages/pourquoiUnePyschotherapie';
import EnPsychotherapie from './pages/enPsychotherapie';


function App() {
  const location = useLocation();
  return (
    <div className="App">
      <GlobalStyle />
      <Nav />
      
      <AnimatePresence exitBeforeEnter>
        <Switch location={location} key={location.pathname}>
          <Route path="/" exact>
          <Home />
          </Route>
          <Route path="/pour-enfants" exact>
            <PourEnfants />
          </Route>
          <Route path="/pour-adultes" exact>
            <PourAdultes />
          </Route>
          <Route path="/pourquoi-une-psychothérapie" exact>
            <PourquoiUnePscyhotherapie />
          </Route>
          <Route path="/en-psychothérapie" exact>
            <EnPsychotherapie />
          </Route>

        </Switch>
      </AnimatePresence>
      <Footer/>
      
    </div>
  );
}

export default App;
